import styled from 'styled-components';

const Label = styled.label`
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 10px;
  display: block;
  font-size: 0.9rem;
  color: #333333;
`;

const H3 = styled.h3`
  font-weight: 600;
  font-size: 1.5rem;
  text-align: center;
  color: #333;
  margin-bottom: 1rem;
  margin-top: 4rem;
`;

const Header = styled.header`
  font-weight: 600;
  font-size: 2.5rem;
  text-align: center;
  color: #333;
  margin-bottom: 1rem;
  margin-top: 2rem;
`;

export { Label, H3, Header }



